export const CalendarScreen = () => {
  
  const dispatch = useDispatch();

  const { events, activeEvent } = useSelector( state => state.calendar );
  const { uid} = useSelector( state => state.auth );


  const [ lastView, setLastView ] = useState(localStorage.getItem('calendar:view') || 'month');
  
  useEffect(() => {
    dispatch(eventStartLoading());
  }, [dispatch]);
  

  const onDoubleClick = (e) => {
    dispatch(uiOpenModal());
  }

  const onSelectEvent = (e) => {
    dispatch(eventSetActive(e))
    
  }
  
  const onViweChange = (e) => {
    setLastView(e);
    localStorage.setItem('calendar:view', e);
  }


  const onSelectSlot = (e) => {
    dispatch({type: types.eventSetActive, payload: null})
  }

  const eventeStyleGetter = (event, start, end, isSelected) => {

    const style = {
      backgroundColor: (uid === event.user._id)? '#900C3F': '#465660',
      borderRadius: '0px',
      opacity: 0.8,
      color: 'white',
      dispaly: 'block'
    }

    return {
      style
    }
  }

  return (
    <div className='calendar-screen'>
      <Navbar />
      <Calendar
        localizer={localizer}
        events={events}
        startAccessor='start'
        endAccessor='end'
        messages={messages}
        eventPropGetter={eventeStyleGetter}
        onDoubleClickEvent={onDoubleClick}
        onSelectEvent={onSelectEvent}
        onSelectSlot = { onSelectSlot }
        selectable = { true }
        onView = { onViweChange }
        view={lastView}
        components={{ event: CalendarEvent }}
      />
      
      <CalendarModal />
      
      {
        activeEvent && <DeleteEventFab />
      }


      <AddNewFab />
    </div>
  )
};
