import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch"
import { prepararEventos } from "../helpers/prepararEventos";
import { types } from "../types/types"


export const eventStartNew = (event) => {

  return async(dispatch,getState)=>{

    try {
      const { uid, name } = getState().auth;
      event.user= uid;
      const resp = await fetchConToken('events',event, 'POST');
  
      const body = await resp.json();

      if (body.ok) {
        event.id = body.evento.id;
        event.user = {
          _id: uid,
          name
        }
        dispatch(eventAddNew(event));
      }
      
    } catch (error) {
      console.log(error);
    }


  }
}

const eventAddNew = (event) => ({
  type: types.eventAddNew,
  payload:event
})

export const eventSetActive = (event) => ({
  type: types.eventSetActive,
  payload:event
})


export const eventStartUpdate = (event) => {

  return async(dispatch)=>{
    try {
    
      const resp = await fetchConToken(`events/${event.id}`,event,'PUT');
      const body = await resp.json();

      if (body.ok) {
        dispatch(eventUpdated(event));
      }else{
        const error_msg = body.errors ? Object.values( body.errors )[0].msg : body.msg
        Swal.fire( 'Error', error_msg, 'error' ); 
      }

    } catch (error) {
      console.log(error);
    }
  }
}


const eventUpdated = (event) => ({
  type: types.eventUpdate,
  payload:event
})



export const eventStartDelete = (event) => {

  return async(dispatch)=>{
    try {
    
      const resp = await fetchConToken(`events/${event.id}`,event,'DELETE');
      const body = await resp.json();

      if (body.ok) {
        dispatch(eventDeleted(event));
      }else{
        const error_msg = body.errors ? Object.values( body.errors )[0].msg : body.msg
        Swal.fire( 'Error', error_msg, 'error' ); 
      }

    } catch (error) {
      console.log(error);
    }

  }
}



const eventDeleted = () => ({
  type: types.eventDelete,
})



export const eventStartLoading = () => {
  return async(dispatch) =>{
    try {
      
      const resp = await fetchConToken('events');
      const body = await resp.json();

      const events = prepararEventos(body.eventos);

      dispatch(eventLoaded(events));

      
    } catch (error) {
      console.log(error);
    }
  }
}


const eventLoaded = (events) => ({
  type: types.eventLoaded,
  payload:events
})



export const eventLogout = () => ({
  type: types.eventLogout
})